*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}

.App-header {
  max-width: 100vw;
  overflow: overlay;
  width:100%;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-right-top{
  display: flex;
  padding: 20px;
  justify-content: flex-end;
}

.login{
  border-left: 1px solid black ;
  margin-left: 10px;
  padding-left: 80px;
  color: #000000;
  font-size: 18px;
}


.header-right-bottom{
  display: flex;
  width:100%;
  justify-content: space-around;
  align-items: center;
}

.navigate{
  margin:10px;
  margin-left: 37px;
  font-size: 20px;
  color: #000000;
  opacity: 60%;
}


.banner{
  width:100%;
  display: flex;
  background-color: #ac0006;
}

.main{
  display: flex;
  flex-wrap: wrap;
}

.banner-box{
  width:50%;
  height:800px;
  position: relative;
  padding:40px;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.banner-text{
  height:100%;
  display: flex; 
  flex-direction: column;
  justify-content: space-around;
}


.banner-title{
  font-family: 'IvyPresto Display';
  font-style: normal;
  font-weight: 400;
  font-size: 80px;
}


.banner-desc{
  width: 80%;
  font-family: 'Niveau Grotesk';
  font-style: normal;
  font-weight: 400;
  line-height: 40px;
  font-size: 18px;
  margin: 26px 0;
}

.portfolio-btn{
  cursor: pointer;
  width:fit-content;
  padding: 10px 20px;
  font-family: 'Niveau Grotesk';
  font-style: normal;
  font-weight: 700;
  font-size: 18.75px;
  background: #D7B344;
  border-radius: 4px;
}

.banner-right{
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 40px;
}

.img-wrap{
  height:100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner-img1{
  width:50%;
  position: absolute;
  left: 150px;
  top: 80px;;
  z-index:3;
  border-radius: 16px;
}

.img-bgc{
  position: absolute;
  top:150px;
  width:100%;
  height:100%;
  max-width: 70%;
  background: #D8B243;
  border-radius: 16px;
}

.banner-img2{
  width:35%;
  max-width: 70%;
  position: absolute;
  bottom: 60px;
  right:60px;
  z-index:2;
  border-radius: 16px;
}

.banner-img3{
  width: 100%;
  height: 100%;
  position: relative;
  left:20px;
  top:20px;
  border-radius: 16px;
}


.footer{
  display: flex;
}

.footer-item{
  width:33.33%;
  text-align: center;
  margin: 100px 20px;
}

.footer-item-title{
  font-size: 37px;
}

.footer-item-desc{
  margin-right: 4px;
}

@media screen and (max-width:600px){

  .banner-title{
    font-size: 40px;
  }
  .banner-img1{
    left:20px;
  }
  .banner-img2{
    right:20px;
    bottom: 20px;
  }
  .banner-desc{
    width:100%;
  }
  .img-bgc{
    height:40%;
  }
  .banner-box{
    width:100vw;
    height:fit-content;
  }
  .footer-item{
    width:100vw;
  }
  .footer{
    flex-wrap: wrap;
  }
}


